.layout-header {
  width: 100%;
  height: 65px;
  padding: $gutter 30px;
  border-bottom: 1px solid $color-hr;
  background-color: $color-white;

  @include flex(center);
  .js-logo {
    a {
      display: block;
      width: 50px;
      height: 50px;
      overflow: hidden;
      border-radius: 50%;
    }
  }
  .header-content {
    margin-left: 40px;
    h1 {
      font-size: 26px;
    }
  }

  .header-right {
    position: absolute;
    right: 40px;
    top: 20px;

    span {
      @include flex(center);
      font-size: 14px;
      color: $color-brand;
      i {
        margin-right: $gutter/2;
        font-size: 20px;
      }
    }

    .rele {
      margin-right: $gutter * 4;
    }

    .produce_src {
      span {
        padding-left: $gutter;
      }
    }
  }

  @media (min-width: 1500px) {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
  }
}
