::-webkit-scrollbar {
  width: 6px;
  height: 4px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: rgba(217, 237, 255, 0.3);
  background: rgba(217, 237, 255, 0.1);
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background: rgba(101, 181, 255, 0.3);
  -webkit-box-shadow: rgba(217, 237, 255, 0.5);
}

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(101, 181, 255, 0.3);
}

body {
  position: relative;
}

.layout-container {
  width: 100%;
  padding-top: 20px;
}

.t-section-title {
  padding-bottom: $gutter * 2;
  border-bottom: 3px dashed $color-hr;
  margin-bottom: 20px;

  span {
    font-size: 18px;
    color: $color-sub;
    margin-left: 20px;
  }
}

.t-types-title {
  font-size: 22px;
  margin: {
    top: 20px;
    bottom: 20px;
  }

  &::before {
    content: "◉";
    display: inline-block;
    margin-right: $gutter;
    color: $color-button;
    font-size: 20px;
  }
}

.t-desc {
  position: relative;
  padding-left: 20px;
  color: $color-sub;
  font-size: 16px;
  margin: 12px 0;

  &::before {
    content: "";
    display: block;
    height: 24px;
    width: 3px;
    background-color: $color-hr;
    border-radius: 4px;
    position: absolute;
    left: 0;
    top: 0;
  }
}

.t-light {
  background-color: $color-card;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0 2px;
  transition: all 0.2s;

  &:hover {
    background-color: $color-button-bg;
    text-decoration: none;
  }
}

.t-light-text {
  padding: 4px 6px;
  background-color: $color-button-bg;
  border-radius: 4px;
  display: inline;
}

.language-html {
  width: 100%;
  margin: auto;
  margin-bottom: 20px;
}

@media (min-width: 1500px) {
  .layout-container {
    width: calc(100% - 220px);
    margin-left: 220px;
    margin-top: 65px;
  }
}

@media (min-width: 1600px) {
  .layout-container {
    width: calc(100% - 340px);
    margin-left: 340px;
  }
}
