.popular-call-action,
.popular-call-action-base {
  h3 {
    font-size: 30px;
    line-height: 1.2;
  }

  .desc {
    display: block;
    margin-top: $gutter * 3;
  }

  .call-action-box {
    margin-top: 30px;
    @include flex(center, center);

    .call-action-btn {
      padding: $gutter $gutter * 3;
      background-color: $color-white;
      box-shadow: 0 3px 6px rgba(#000, 0.16);
      border-radius: 6px;
      overflow: hidden;

      > a {
        @include flex(center);
      }

      span {
        font-size: 16px;
        display: inline-block;
      }

      img {
        width: 30px;
        flex-shrink: 0;
        margin-right: 14px;
      }

      &:nth-of-type(1) {
        margin-right: 17px;
      }

      &:nth-of-type(2) {
        margin-left: 17px;
      }
    }
  }

  @include media-breakpoint-up(xl) {
    border-radius: $gutter;
    overflow: hidden;

    h3 {
      font-size: 36px;
    }

    .desc {
      font-size: 20px;
    }
  }

  @include media-breakpoint-down(sm) {
    .call-action-box {
      flex-wrap: wrap;

      .call-action-btn {
        width: 100%;
        max-width: 400px;

        > a {
          justify-content: center;
        }

        margin: {
          left: 0 !important;
          right: 0 !important;
        }

        &:nth-of-type(1) {
          margin-bottom: 22px;
        }
      }
    }
  }

  @include media-breakpoint-down(mc) {
    h3 {
      font-size: 24px;
    }

    .desc {
      font-size: 16px;
    }

    .call-action-box {
      .call-action-btn {
        padding: {
          left: $gutter + 2px;
          right: $gutter + 2px;
        }

        span {
          font-size: 15px;
        }
      }
    }
  }
}

.popular-call-action {
  background-color: $color-card;

  > .container {
    @include flex(center, center, wrap);
    padding: $gutter * 4 $gutter * 3 46px $gutter * 3;
  }

  .desc {
    text-align: center;
  }

  @include media-breakpoint-up(md) {
    > .container {
      padding: $gutter * 4 60px 46px 60px;
    }
  }

  @include media-breakpoint-up(lg) {
    > .container {
      padding: $gutter * 4 114px 46px 114px;
    }
  }

  @include media-breakpoint-down(mc) {
    > .container {
      padding: {
        left: $gutter * 2;
        right: $gutter * 2;
      }
    }
  }
}

.popular-call-action-base {
  padding: 52px $gutter * 2 34vw $gutter * 2;
  border-radius: $border-radius * 2;
  overflow: hidden;

  background: {
    repeat: no-repeat;
    size: cover;
    position: bottom right;
  }

  .call-action-box {
    .call-action-btn {
      border-radius: 50px;
    }
  }

  @include media-breakpoint-up(md) {
    padding: 40px 200px 36px 34px;

    .call-action-box {
      justify-content: flex-start;
    }
  }

  @include media-breakpoint-up(lg) {
    padding-right: 260px;
  }

  @include media-breakpoint-up(xl) {
    padding: 40px 400px 36px 80px;
  }

  @include media-breakpoint-down(md) {
    .call-action-box {
      flex-wrap: wrap;

      .call-action-btn {
        margin-left: 0 !important;
        margin-right: 0 !important;
        &:nth-of-type(1) {
          margin-bottom: 22px;
        }
      }
    }
  }

  @media (max-width: 460px) {
    padding-bottom: 64vw;
  }

  @include media-breakpoint-down(mc) {
    padding: {
      left: $gutter;
      right: $gutter;
    }
  }
}

.mb-screen {
  @include media-breakpoint-down(lg) {
    padding: 0;
  }
}
