.layout-sidenav {
  padding: $gutter * 5 $gutter * 3;
  width: 320px;
  height: calc(100vh + 65px);
  overflow-y: auto;
  border-right: 1px solid $color-hr;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 8;
  background-color: $color-white;

  header {
    margin-bottom: $gutter * 2;
  }

  footer {
    height: 100px;
  }

  .s-summary {
    height: 100%;
  }

  .s-summary-content {
    height: 100%;
  }

  .s-summary-list {
    li {
      a {
        display: inline-block;
        background-color: transparent;
        transition: all 0.2s;
        width: 100%;
        border-radius: 4px;
        padding: 10px $gutter * 2;
        text-decoration: none;
        font-size: 16px;
        color: $color-sub;

        &:hover,
        &.active {
          background-color: $color-card;
          color: $color-brand;
        }

        span {
          padding-right: $gutter;
        }
      }

      ul {
        margin-left: 30px;
      }
    }
  }

  .close-btn {
    width: 38px;
    height: 38px;
    position: absolute;
    right: $gutter;
    top: $gutter;
    border: 1px solid transparent;
    @include flex(center, center);
    border-radius: 10px;
    transition: all 0.26s ease;

    i {
      font-size: 28px;
    }

    &:hover {
      border-color: $color-text-hover;
      border-radius: 50%;
      i {
        color: $color-brand;
      }
    }
  }

  @media (min-width: 1500px) {
    margin-top: 65px;
    width: 220px;
    height: calc(100vh - 65px);
    padding: $gutter * 5 20px;

    .close-btn {
      display: none;
    }
  }

  @media (min-width: 1600px) {
    width: 340px;
    padding: $gutter * 5 $gutter * 4;
  }

  @media (max-width: 1499px) {
    transition: transform 0.7s cubic-bezier(0.19, 1, 0.22, 1);
    transform: translateX(-105%);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
      0 3px 1px -2px rgba(0, 0, 0, 0.2);

    .s-summary-list {
      li.home {
        display: block !important;
        a {
          font-size: 18px;
        }
      }
    }
  }
}

#sidenav-btn {
  position: fixed;
  right: 30px;
  bottom: 40px;
  z-index: 999;
  width: 56px;
  height: 56px;
  padding: 0;
  @include flex(center, center);
  background-color: #26a69a;
  border-radius: 50%;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);

  i {
    color: $color-white;
    font-size: 26px;
  }

  &:active {
    background-color: rgba(#26a69a, 0.85);
  }

  @media (min-width: 1500px) {
    display: none;
  }
}
