.subscribe-wrapper {
  position: relative;
  height: auto;
  transition: height 0.35s ease-out;
  overflow: hidden;

  &.module-deck {
    padding: 0;
  }

  .free-subscribe,
  .email-subscribe {
    h4 {
      font-size: 20px;
      margin-bottom: 8px;
    }
  }

  .free-subscribe {
    transition: all 0.45s ease-in-out;

    .news-desc {
      display: inline;
    }

    .more {
      font-size: 14px;
      color: $color-light-brand;
      text-decoration: unset;
      padding-left: 6px;
      &:hover {
        text-decoration: underline;
      }
    }

    .sub-list {
      text-align: center;
      margin-top: 4px;
    }

    .like-btn {
      display: inline-block;
      text-align: center;
      border: 1px solid $color-light-brand;
      color: $color-light-brand;
      min-height: 34px;
      min-width: 254px;
      border-radius: 22px;
      padding: $gutter $gutter * 2;
      font-size: 14px;
      background-color: transparent;
      margin-top: 12px;
      transition: all 0.35s ease;

      &:hover {
        background-color: $color-light-brand;
        color: $color-white;
      }
    }
  }

  .email-subscribe {
    background-color: $color-white;
    min-height: 100%;
    width:100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4;

    transition: all 0.35s ease-in-out;

    h4 {
      font-weight: 500;
    }

    .sub-list {
      display: flex;
      flex-wrap: wrap;

      label {
        margin-bottom: $gutter;
        width: 50%;
      }

      .checkbox + span {
        line-height: 24px;

        &::after {
          border-color: $color-light-brand;
        }
      }

      span {
        font-size: 14px;
      }
    }

    .sub-email {
      display: flex;
      margin-top: $gutter/2;
      height: 34px;
      overflow: hidden;

      label,
      br {
        display: none;
      }

      input {
        border: 0;

        &[type="submit"] {
          background-color: $color-brand;
          color: $color-white;

          padding: {
            left: 18px;
            right: 22px;
          }

          border-top-right-radius: 18px;
          border-bottom-right-radius: 18px;
        }

        &[type="email"] {
          width: calc(100% - 106px);
          padding-left: $gutter * 2;
          border: 1px solid $color-brand;
          border-top-left-radius: 18px;
          border-bottom-left-radius: 18px;
          outline: none;
        }
      }
    }

    .news-privacy {
      margin-top: $gutter;

      a {
        color: $color-brand;
      }

      input[type="checkbox"] {
        width: 16px;
        height: 16px;
        margin-right: 4px;
        transform: translateY(2px);
      }

      span{
        line-height: 24px;
      }
    }

    .back-options {
      margin: auto;
      display: block;
      margin-top: 6px;
      background-color: transparent;
      color: $color-brand;

      i {
        display: inline-block;
        transform: translateY(5px);
      }
    }
  }

  .mc4wp-response{
    margin-top:$gutter + 2px;
    
  }
}

.sub-event {
  .email-subscribe {
    opacity: 0;
    transform: translateX(100%);
  }

  &.change {
    .free-subscribe {
      transform: translateX(-30%);
    }

    .email-subscribe {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

//  侧边栏订阅兼容
.panel-aside {
  .subscribe-wrapper {
    overflow: hidden;
    margin-bottom: $gutter * 3;

    .free-subscribe,
    .email-subscribe {
      padding: 0px;
    }
  }

  @include media-breakpoint-down(xl){
    .subscribe-wrapper {
      .email-subscribe .sub-list label{
        width:100%;
      }
    }
  }
}


