.article-box {
  margin-bottom: -24px;
  .article-card {
    margin-bottom: 24px;
  }

  .article-card-cover {
    position: relative;
    width: 100%;
    overflow: hidden;
    border-radius: 4px;

    a {
      display: block;
      padding-bottom: 62.25%;
      overflow: hidden;
    }

    img {
      display: block;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      transition: transform 0.6s;
    }

    &:hover {
      img {
        transform: translateX(-50%) scale(1.08);
      }
    }
  }

  .article-card-title {
    font-size: 18px;
    line-height: 1.33em;
    color: #292929;
    margin-top: 16px;
    @include limit-line(3, 1.33);
    @media (max-width: 241px) {
      font-size: 16px;
      font-weight: 400;
    }
  }

  .article-card-description {
    margin-top: 16px;
    font-size: 16px;
    font-weight: 400;
    color: #4c4c4c;
    line-height: 1.4em;
  }
}
